import React from 'react';
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import moment from 'moment';
import { setSelectedTime } from "../../models/actions/time-list.actions";
import { deselectService } from "../../models/actions/services.actions";
import { getSelectedServicesFromState, getSelectedServicesTotalSum } from "../../helpers/services-helper";
import styles from './confirmation.module.scss';
import closeIcon from '../../assets/icons/delete.svg';
import user from '../../assets/user.svg';
import phone from '../../assets/phone.svg';
import ActionPanel from "../../modules/action-panel/action-panel.module";
import editIcon from '../../assets/icons/pencil.svg';
import infoIcon from '../../assets/icons/info-icon--yellow.svg';

const baseClassName = 'confirmation';

const Confirmation = props => {
  const { orderInfo, selectedServices, handleDeselect } = props;
  const currency = selectedServices[0] && selectedServices[0].currency;

  return (
    <div className={styles[baseClassName]}>
      <header className={styles[`${baseClassName}__header`]}>
        <h1 className={styles[`${baseClassName}__header__headline`]}>Підтвердження бронювання</h1>
      </header>

      <div className={styles[`${baseClassName}__order-info`]}>
        <div className={styles[`${baseClassName}__order-info__table`]}>
          <h4 className={styles[`${baseClassName}__order-info__table__headline`]}>Тренер:</h4>
          <div className={styles[`${baseClassName}__order-info__table__row`]}>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <strong>{orderInfo.selectedEmployee.name === "немає значення" ? "Тренер не вибраний" : orderInfo.selectedEmployee.name}</strong>
            </div>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <Link
                to="/"
                className={styles[`${baseClassName}__order-info__table__row__item__button`]}
              >
                <img src={editIcon} alt="змінити тренера" title="змінити тренера" />
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className={styles[`${baseClassName}__order-info`]}>
        <div className={styles[`${baseClassName}__order-info__table`]}>
          <h4 className={styles[`${baseClassName}__order-info__table__headline`]}>час та дата:</h4>
          <div className={styles[`${baseClassName}__order-info__table__row`]}>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <strong>{
                `${moment(orderInfo.selectedDate).format('DD.MM.YYYY')} / ${orderInfo.selectedTime && orderInfo.selectedTime.value}`
              }</strong>
            </div>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <Link
                to="/select-date-time"
                className={styles[`${baseClassName}__order-info__table__row__item__button`]}
              >
                <img src={editIcon} alt="змінити час / дату" title="змінити час / дату" />
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className={styles[`${baseClassName}__order-info`]}>
        <div className={styles[`${baseClassName}__order-info__table`]}>
          <h4 className={styles[`${baseClassName}__order-info__table__headline`]}>Список послуг:</h4>
          <div className={styles[`${baseClassName}__order-info__table__order-table`]}>
              {
                selectedServices && selectedServices.map(({name, price, id}) => (
                  <div key={id} className={styles[`${baseClassName}__order-info__table__order-table-row`]}>
                    <div className={styles[`${baseClassName}__order-info__table__order-table-row__cell`]}>
                      {name}
                    </div>
                    <div className={styles[`${baseClassName}__order-info__table__order-table-row__cell`]}>
                      <span className={styles[`${baseClassName}__order-table__price-value`]}>
                        {`${price} ${currency}`}
                      </span>
                      <button onClick={() => handleDeselect(id)}>
                        <img alt="close icon" src={closeIcon}/>
                      </button>
                    </div>
                  </div>
                ))
              }
          </div>
        </div>
      </div>

      <div className={styles[`${baseClassName}__order-info`]}>
        <div className={styles[`${baseClassName}__order-info__table`]}>
          <div className={styles[`${baseClassName}__order-info__table__row`]}>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <strong>Загальна сума</strong>
            </div>
            <div className={styles[`${baseClassName}__order-info__table__row__item`]}>
              <strong>{`${getSelectedServicesTotalSum(selectedServices)} ${currency}`}</strong>
            </div>
          </div>
        </div>
      </div>

      <p className={styles[`${baseClassName}__alarm`]}>
        <img
          src={infoIcon}
          alt="info icon"
          className={styles[`${baseClassName}__alarm__icon`]}
        />
        Для того щоб підтвердити замовленя вам потрібно ввести номер телефону та імя
      </p>

      <div className={styles[`${baseClassName}__form`]}>
        <div className={styles[`${baseClassName}__form__item`]}>
          <input
            type="email"
            id="confirmPhoneNumber"
            placeholder="Введіть номер телефону"
            className={styles[`${baseClassName}__form__item__input`]}
          />
        </div>
        <div className={styles[`${baseClassName}__form__item`]}>
          <input
            type="tel"
            id="confirmCustomerName"
            placeholder="Введіть своє імя"
            className={styles[`${baseClassName}__form__item__input`]}
          />
        </div>
      </div>
      <ActionPanel path="/congratulations"/>
    </div>
  );
}

const mapDispatchToProps = dispatch => ({
  handleSelect: data => dispatch(data => data),
  handleDeselect: data => dispatch(deselectService(data)),
})

const mapStateToProps = state => ({
  orderInfo: state.orderInfo,
  state: state,
  selectedServices: state.services &&
    state.services.list &&
    state.orderInfo &&
    state.orderInfo.selectedServices &&
    getSelectedServicesFromState(state.services.list, state.orderInfo.selectedServices),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Confirmation);